.weather-widget {
  font-family: Arial, sans-serif;
  padding: 10px;
  margin: 5px 0px 5px 0px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
  display: flex; /* Row layout for child elements */
  flex-direction: column; /* Ensure overall widget content flows top to bottom */
  align-items: center; /* Center align content */
}

.widget-location {
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 10px; /* Add spacing below the location name */
}

.widget-daily {
  display: flex; /* Row layout for daily entries */
  flex-direction: row;
  gap: 15px; /* Add spacing between columns */
  overflow-x: auto; /* Add horizontal scrolling if needed */
  padding: 10px 0;
  width: 100%;
}

.daily-weather {
  flex: 1 0 20%; /* Make each column occupy an equal share of space */
  display: flex;
  flex-direction: column; /* Ensure daily entries stack within their column */
  text-align: center; /* Center align text inside columns */
  padding: 10px;
  border: 1px solid #ddd; /* Optional: add a border for each column */
  border-radius: 8px; /* Rounded corners for columns */
  background-color: #fff; /* Background color for columns */
}

.widget-date {
  font-size: 1rem;
  font-weight: bold;
  margin-bottom: 5px; /* Add spacing below date */
}

.widget-summary,
.widget-temp-high,
.widget-temp-low,
.widget-wind-speed,
.widget-precip {
  font-size: 0.9rem;
  margin: 5px 0; /* Add spacing between rows */
  color: #333; /* Text color */
}

.widget-loading,
.widget-error {
  color: #ff0000;
  font-size: 0.9rem;
}
